<template>
  <div>
    <div style="margin-left: 20px">
      <div>
        Select Robot
      </div>
      <a-row>
        <a-col :span="10">
          <a-select
            @change="handleRobotSelectChange"
            v-model="selectedRobot"
            style="width: 150px"
          >
            <a-select-option
              v-for="robot in robotList"
              :key="robot"
              :value="robot"
            >
              Robot &emsp; {{ robot }}
            </a-select-option>
          </a-select>
        </a-col>
      </a-row>
    </div>

    <div style="margin-left: 20px">
      <div v-if="status" style="margin-top: 30px">
        Status: {{ status }} {{ status == 'collided' ? 'with ' + collidedWith : ''}}
      </div>
    </div>

    <div style="margin-left: 20px">
      <div v-if="heading && selectedRobot == heading.robot_id" style="margin-top: 30px">
        Its heading: {{ heading.heading }}
      </div>
    </div>

    <a-row style="margin-top: 30px">
      <a-button
        :type="mode == 'Auto' ? 'primary' : ''"
        @click="handleAutoModeBtnClick"
      >
        Auto Mode
      </a-button>
      <a-button
        :type="mode == 'Manual' ? 'primary' : ''"
        @click="handleManualModeBtnClick"
        style="margin-left: 5px"
      >
        Manual Mode
      </a-button>
    </a-row>
  </div>
</template>

<script>
export default {
  props: ["robotList", "heading", "status", "collidedWith"],
  data() {
    return {
      sectionList: [
        {
          name: "Available",
          id: 1,
        },
        {
          name: "Unavailable",
          id: 2,
        },
      ],
      directionList: [
        {
          name: "North",
          id: 1,
        },
        {
          name: "South",
          id: 2,
        },
        {
          name: "West",
          id: 3,
        },
        {
          name: "East",
          id: 4,
        },
      ],
      mode: "",
      selectedRobot: null,
    };
  },
  methods: {
    handleAutoModeBtnClick() {
      if (!this.selectedRobot)
        this.$notification.error({
          message: "No robots selected! Please select one robot.",
        });
      else {
        this.mode = "Auto";
        this.$emit("modeSelection", { mode: "auto" });
      }
    },
    handleManualModeBtnClick() {
      if (!this.selectedRobot)
        this.$notification.error({
          message: "No robots selected! Please select one robot.",
        });
      else {
        this.mode = "Manual";
        this.$emit("modeSelection", { mode: "manual" });
      }
    },
    handleRobotSelectChange(selectedRobot) {
      this.$emit("robotChange", selectedRobot);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped></style>
