<template>
  <div>
    <div style="text-align: center">
      <v-icon
        @mousedown="handleObject('pick_up')"
        @mouseup="handleMouseUp"
        name="upload"
        color="blue"
        scale="8"
        style="cursor: pointer"
      />
    </div>
    <div style="text-align: center">Pick Up Object</div>

    <div style="text-align: center; margin-top: 40px">
      <v-icon
        @mousedown="handleObject('drop')"
        @mouseup="handleMouseUp"
        name="download"
        color="blue"
        scale="8"
        style="cursor: pointer"
      />
    </div>
    <div style="text-align: center">Drop Object</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedRobot: null,
    };
  },
  methods: {
    handleObject(movement) {
      if (!this.selectedRobot)
        this.$notification.error({
          message: "No robots selected! Please select one robot.",
        });
      else {
        let payload = {
          action: movement,
          robot_id: this.selectedRobot,
        };
        this.$socket.emit("manual", payload);
        this.$emit("manualMode");
      }
    },
    handleMouseUp() {
      let payload = {
          action: "stop",
          robot_id: this.selectedRobot,
        }
      this.$emit("mouseUp", payload);
    }
  },
};
</script>

<style scoped></style>
