<template>
  <div>
    <h1 style="text-align: center">Navigation Panel</h1>
    <div style="text-align: center">
      <v-icon
        @mousedown="handleNavigation('forward')"
        @mouseup="handleMouseUp"
        name="arrow-up"
        color="blue"
        scale="4"
        style="cursor: pointer"
      />
    </div>

    <div style="text-align: center; margin-top: 50px">
      <span style="float: left">
        <v-icon
          @mousedown="handleNavigation('left')"
          @mouseup="handleMouseUp"
          name="undo"
          color="blue"
          scale="4"
          style="cursor: pointer"
      /></span>
      <v-icon
        name="redo"
        color="blue"
        scale="4"
        style="cursor: pointer; visibility:hidden"
      />
      <span style="float: right">
        <v-icon
          @mousedown="handleNavigation('right')"
          @mouseup="handleMouseUp"
          name="redo"
          color="blue"
          scale="4"
          style="cursor: pointer"
      /></span>
    </div>

    <div style="text-align: center; margin-top: 50px">
      <v-icon
        @mousedown="handleNavigation('backward')"
        @mouseup="handleMouseUp"
        name="arrow-down"
        color="blue"
        scale="4"
        style="cursor: pointer"
      />
    </div>
  </div>
</template>

<script>
export default {
  props: {},
  data() {
    return {
      selectedRobot: null,
    };
  },
  methods: {
    handleNavigation(movement) {
      if (!this.selectedRobot)
        this.$notification.error({
          message: "No robots selected! Please select one robot.",
        });
      else {
        let payload = {
          action: movement,
          robot_id: this.selectedRobot,
        };
        this.$socket.emit("manual", payload);
        this.$emit("manualMode");
      }
    },
    handleMouseUp() {
      let payload = {
          action: "stop",
          robot_id: this.selectedRobot,
        }
      this.$emit("mouseUp", payload);
    }
  },
};
</script>

