<template>
  <div>
    <a-steps class="red" :current="3">
      <a-popover slot="progressDot" slot-scope="{ index, status, prefixCls }">
        <template slot="content">
          <div v-if="index != 1">Section color: red</div>
          <div v-if="index != 1">Section id: {{ !index ? 1 : 2 }}</div>
          <div v-else>Intersection</div>
        </template>
        <span
          id="center-1"
          :style="
            index != 1
              ? 'transform: scale(2.5); background-color: red; cursor: pointer'
              : 'background-color: black'
          "
          :class="`${prefixCls}-icon-dot`"
        />
      </a-popover>
      <a-step
        @click="(e) => handlePackageClick(e, 'red', 1)"
        title="Package 1R"
      />
      <a-step />
      <a-step
        @click="(e) => handlePackageClick(e, 'red', 2)"
        title="Package 2R"
      />
    </a-steps>

    <a-steps class="yellow" style="margin-top: 110px" :current="3">
      <a-popover slot="progressDot" slot-scope="{ index, status, prefixCls }">
        <template slot="content">
          <div v-if="index != 1">Section color: yellow</div>
          <div v-if="index != 1">Section id: {{ !index ? 1 : 2 }}</div>
          <div v-else>Intersection</div>
        </template>
        <span
          id="center-2"
          :style="
            index != 1
              ? 'transform: scale(2.5); background-color: yellow; cursor: pointer'
              : 'background-color: black'
          "
          :class="`${prefixCls}-icon-dot`"
        />
      </a-popover>
      <a-step
        @click="(e) => handlePackageClick(e, 'yellow', 1)"
        title="Package 1Y"
      />
      <a-step />
      <a-step
        @click="(e) => handlePackageClick(e, 'yellow', 2)"
        title="Package 2Y"
      />
    </a-steps>

    <a-steps class="green" style="margin-top: 110px" :current="3">
      <a-popover slot="progressDot" slot-scope="{ index, status, prefixCls }">
        <template slot="content">
          <div v-if="index != 1">Section color: green</div>
          <div v-if="index != 1">Section id: {{ !index ? 1 : 2 }}</div>
          <div v-else>Intersection</div>
        </template>
        <span
          :style="
            index != 1
              ? 'transform: scale(2.5); background-color: green; cursor: pointer'
              : 'background-color: black'
          "
          :class="`${prefixCls}-icon-dot`"
        />
      </a-popover>
      <a-step
        @click="(e) => handlePackageClick(e, 'green', 1)"
        title="Package 1G"
      />
      <a-step />
      <a-step
        @click="(e) => handlePackageClick(e, 'green', 2)"
        title="Package 2G"
      />
    </a-steps>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selectedRobot: null,
      mode: null
    };
  },
  mounted() {},
  methods: {
    handlePackageClick(evt, color, id) {
      evt.stopPropagation();
      if (this.mode != "auto")
        this.$notification.error({
          message: "This action is only available in auto mode!",
        });
      else {
        let payload = {
          section_color: color,
          section_id: id,
        };
        this.$emit("packageClick", payload);
      }
    },
  },
};
</script>

<style>
.red
  .ant-steps-item-finish
  > .ant-steps-item-container
  > .ant-steps-item-tail::after {
  background-color: red;
}

.yellow
  .ant-steps-item-finish
  > .ant-steps-item-container
  > .ant-steps-item-tail::after {
  background-color: yellow;
}

.green
  .ant-steps-item-finish
  > .ant-steps-item-container
  > .ant-steps-item-tail::after {
  background-color: green;
}
</style>
